<template>
  <div>
    <div class="main-header">
      <div
              class="content-bg-wrap bg-account"
              style="background-image:url('/image/epl_refree.jpg'); background-blend-mode: overlay;background-color: rgba(0,0,0,0.63);"
      ></div>
      <div class="container">
        <div class="versus">
          <div class="row" style="border:solid 1px transparent;position: relative;top: 40px">
            <div class="col">
              <div v-if="home_logo === '' || home_logo === 'undefined' " class="versus-img"></div>
              <div v-else class="versus-img">
                <img :src="home_logo" />
              </div>
            </div>
            <div class="col-6">
              <div class="caption">
                <h1>VS</h1>
              </div>
            </div>
            <div class="col">
              <div v-if="away_logo === '' || away_logo === 'undefined' " class="versus-img"></div>
              <div class="versus-img">
                <img :src="away_logo" />
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="img-bottom"></div>
    </div>

    <div class="space"></div>

    <div class="container" ref="bodyloady">
      <div class="row">
        <LeftSideInfo />
        <div class="col col-xl-6 order-xl-2 col-lg-12 order-lg-1 col-md-12 col-sm-12 col-12">
          <NewsFeed :fixtureId="fixture_id" />
        </div>
        <RightSideInfo />
      </div>
    </div>

    <Footer />
    <a class="back-to-top" href="javascript:;">
      <img src="/svg-icons/back-to-top.svg" alt="arrow" class="back-icon" />
    </a>
  </div>
</template>

<script>
import LeftSideInfo from "../../components/Home/LeftSideInfo";
import RightSideInfo from "../../components/Home/RightSideInfo";
import Footer from "../../components/Home/Footer";
import NewsFeed from "../../components/Home/NewsFeed";
import ls from "../../localstorage";

var loader;
var pageData = {
  projectName: "",
  fixture_id: "",
  home_logo: "",
  away_logo: "",
  home_team_name: "",
  away_team_name: "",
};

export default {
  name: "Match",
  data() {
    return pageData;
  },
  components: { LeftSideInfo, RightSideInfo, NewsFeed, Footer },
  created() {
    this.fixture_id = this.$route.params.fixture_id;
  },
  methods: {
    showLoading() {
      let loader = this.$loader(this, this.$refs.loadContents);
      return loader;
    },
    getDetails() {
      var fix_id = this.fixture_id;

      this.axios
        .get("fixtures/info/" + fix_id)
        .then(response => {
          var data = response.data.data;
          // console.log(data.home_team_name);
          this.home_logo = data.home_team_logo;
          this.away_logo = data.away_team_logo;
          this.home_team_name = data.home_team_name;
          this.away_team_name = data.away_team_name;
          $("title").html( data.home_team_name + " VS " + data.away_team_name + " / " + this.$store.state.projectName);
        })
        .catch(error => {
          this.errorData = {
            message: error.response
              ? error.response.data.message +
                " Sorry we had a problem getting the data requested for"
              : "Sorry an error occured, check your internet",
            isError: true
          };
        });
    }
  },
  mounted() {
    this.projectName = this.$store.state.projectName;
    $("#site-header").addClass("switched");
    this.getDetails();
    $(".back-to-top").on("click", function() {
      $("html,body").animate(
        {
          scrollTop: 0
        },
        1200
      );
      return false;
    });
    $("title").html( this.home_team_name + " VS " + this.away_team_name + " / " + this.$store.state.projectName);

  },
};
</script>

<style>
.versus {
}
.versus-img {
  width: 100%;
  display: flex;
  justify-content: center;
}
.versus-img img {
  width: 60%;
  width: 120px;
  max-width: 120px;
  max-height: 150px;
}
.versus .caption {
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}
.caption h1 {
  color: #fff;
  font-weight: bold;
  text-shadow: 0 5px 8px rgba(0, 0, 0, 0.4);
}
@media (max-width: 560px) {
  .versus-img img {
    max-width: 120%;
    width: 120px;
    max-height: 150px;
  }
  .versus .caption {
    color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
  }
  .caption h1 {
    color: #fff;
    font-weight: bold;
    text-shadow: 0 5px 8px rgba(0, 0, 0, 0.4);
  }
}
</style>
